// The base is where all of the selector will be use from anchor
// <a> tag down to divs and paragraph restrictions
// of this is you can't declare ids and class here
@import "../partial/margin";
@import "../partial/color";
@import "../partial/font";

body {
  background-color: $body-background;
}

/**
* Header Tags
*
*/
h1,
h2,
h3,
h4,
h5 {
  color: $primary-color;
  font-weight: $f700;
}

a {
  cursor: pointer;
  color: $text-color;

  &:hover {
    text-decoration: $none;
    color: inherit;
  }
}

p {
  margin-top: $top10;
}
label {
  margin-bottom: $bot10;
}

p,
label {
  font-size: $f12;
  color: $primary-color;
}

small {
  color: $primary-color;
}
ul li {
  list-style: $none;
  color: $primary-color;
}

// need to check for backward compatability in other browsers
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: $none;
  margin: $zeromarg;
}
input[type="number"] {
  -moz-apperance: textfield;
  -webkit-appearance: textfield;
}

// Tables and Table header
th {
  color: $table-color;
  font-weight: $fbold;
  font-size: $f12;

  td {
    font-weight: $f400;
    font-size: $f15;
  }
}

span {
  margin-left: $left10;
}
strong {
  margin-left: $left10;
}
