// all layout structure of the sites the ids and classes that 
// the site will included

.plus-employee {
    margin-bottom: $bot10;
}
.settings-link {
    margin-right: $right50;
}
.users-link {
    color: $primary-color;
}

.attendance-link {
    color: $calendar;
}

.ratings-link {
    color: $chart;
}
.login-button {
    height: 62px;
    background-color: $primary-color;
    border: $none;
    border-radius: 0em;

    &:hover {
        background-color: $primary-color;
    }
}
.admin-login {
    margin-bottom: $bot20;
}
.forgot-password {
    margin-left: $left50;
}
.alert {
    margin-top: $top20;

    .alert-link {
        font-weight: $f400;
    }
}
.login-forms {
    margin-top: $top120;
}
.card-col {
    margin-top: $top50;
}
.edit-users-profile {
    font-size: $f15;
}
.list-employee {
    font-weight: $f400;
    font-size: $f15;
    margin-left: 4px;
    margin-bottom: $zeromarg;
    color: $primary-color;
}
.nav-item a  {
    padding-right: $left20;
} 
.nissan-evaluation-brand {
    margin-left: $left210;
}